<script>
const HanziWriter = require("hanzi-writer");
import loop from './mixin'
import tangShi from './tangs'
export default {
  mixins: [loop],
  components:{
    tangShi
  },
  data() {
    return {
      nitext: "",
      xun:'',
      myNametext: "",
      myName: [],
      test:'',
      disabledBtn:false,
    };
  },
  methods: {
    animatefun1() {
      this.nitext.animateCharacter();
    },
    animatefun2() {
      this.disabledBtn = true;
      let delayBetweenAnimations = 1000; // milliseconds
      this.myName.map(item => item.hideCharacter());
      this.loop(0, this.myName);
    },
    animatefun3() {
      this.test.quiz();
    }
  },
  mounted() {
    
    this.nitext = HanziWriter.create("character-ni", "", {
      width: 100,
      height: 100,
      padding: 20,
      strokeColor: "#EE00FF", // 
      radicalColor: "#168F16", // 
      showOutline: true, //
      strokeAnimationSpeed: 3, // (1)
      delayBetweenStrokes: 100, // (,1000)
    });

    this.xun = HanziWriter.create("character-xun", "", {
      width: 100,
      height: 100,
      padding: 20,
      strokeColor: "#EE00FF", // 
      radicalColor: "#168F16", // 
      showOutline: true, //
      strokeAnimationSpeed: 3, // (1)
      delayBetweenStrokes: 100, // (,1000)
      delayBetweenLoops: 300//
    });
    this.xun.loopCharacterAnimation()
    //
    let a = this.myNametext.split("");
    a.forEach((item, index) => {
      this.myName[index] = HanziWriter.create(
        `character-target-${index}`,
        item,
        {
          width: 100,
          height: 100,
          padding: 20,
          strokeColor: "#EE00FF", // 
          radicalColor: "#168F16", // 
          showOutline: true, //
          strokeAnimationSpeed: 3, // (1)
          delayBetweenStrokes: 10 // (,1000)
        }
      );
    });
    //
    this.test = HanziWriter.create("character-target-test", "", {
      width: 100,
      height: 100,
      padding: 20,
      showCharacter: false,
    });
    this.animatefun3()
    
  }
};
</script>

